/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
.nav-wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  .nav-direct-links{
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    li:nth-child(-n+3){
      border-right:2px solid $white;
      padding-right:2rem;
      padding-left:2rem;
    }
    li:nth-child(4){
      padding-left: 2rem;
    }
    li{
      list-style: none;

      a{
        text-decoration: none;
        color:$white;
      }
    }
  }
}