/*--------------------------------------------------------------
# Structure
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Colors
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Normalize / CSS Reset
--------------------------------------------------------------*/
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
.nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: center; }
  .nav-wrapper .nav-direct-links {
    display: flex;
    justify-content: space-between;
    text-transform: uppercase; }
    .nav-wrapper .nav-direct-links li:nth-child(-n+3) {
      border-right: 2px solid #FFFFFF;
      padding-right: 2rem;
      padding-left: 2rem; }
    .nav-wrapper .nav-direct-links li:nth-child(4) {
      padding-left: 2rem; }
    .nav-wrapper .nav-direct-links li {
      list-style: none; }
      .nav-wrapper .nav-direct-links li a {
        text-decoration: none;
        color: #FFFFFF; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
body {
  color: #FFFFFF; }

p {
  margin: 0;
  color: black; }

h2 {
  color: black; }

.margin {
  margin-left: 5%;
  margin-right: 5%; }

.tb {
  margin-top: 5%;
  margin-bottom: 5%; }

.white-box {
  min-height: 30vw;
  background-color: white;
  padding: 3rem 1rem 3rem 2rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.6); }

.hide {
  display: none; }

.privacy p, .terms p {
  margin-bottom: 1rem; }

.privacy li, .terms li {
  margin-bottom: 1rem; }

/*--------------------------------------------------------------
# Order Page
--------------------------------------------------------------*/
.loader {
  display: none;
  width: 200px;
  margin: 0 auto;
  position: fixed;
  bottom: 35%;
  left: 50%;
  transform: translate(-50%, -50%); }
  .loader img {
    width: 100%; }

/*--------------------------------------------------------------
# CVV BOX
--------------------------------------------------------------*/
.cvv-button {
  border: none;
  background: none;
  color: black;
  text-decoration: underline; }

.show {
  display: block !important; }

.cvv-box {
  display: none;
  position: fixed;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
  bottom: 15%;
  left: 12%; }
  .cvv-box span {
    font-size: 1.15rem;
    font-weight: bold; }
  .cvv-box p, .cvv-box img {
    padding-bottom: 1rem;
    color: black; }
  .cvv-box .close-cvv {
    position: absolute;
    right: 1%;
    border: none;
    color: black;
    font-size: 2rem;
    top: 1%; }

/*--------------------------------------------------------------
# Thank You Page
--------------------------------------------------------------*/
.thank-you-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5%; }
  .thank-you-page h2 {
    color: #0084ff;
    font-size: 3.5rem; }
  .thank-you-page .thank-you-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .thank-you-page .thank-you-wrapper p {
      padding-bottom: 1rem;
      font-size: 1.25rem; }
    .thank-you-page .thank-you-wrapper .number-generator {
      text-align: center; }
      .thank-you-page .thank-you-wrapper .number-generator p:nth-child(2) {
        font-size: 2rem;
        font-weight: bold; }

.privacy h1, .privacy h2, .privacy h3, .terms h1, .terms h2, .terms h3, .contact-page h1, .contact-page h2, .contact-page h3 {
  color: black; }

.privacy p, .terms p, .contact-page p {
  margin-bottom: 1rem; }

.privacy li, .terms li, .contact-page li {
  margin-bottom: 1rem;
  color: black; }

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 2rem; }
  footer .footer-wrapper {
    flex-basis: 100%;
    display: flex;
    align-items: center;
    flex-direction: column; }
    footer .footer-wrapper .footer-index {
      width: 432px;
      display: flex;
      justify-content: space-between; }
      footer .footer-wrapper .footer-index a {
        color: black;
        text-decoration: none; }
    footer .footer-wrapper p {
      padding-top: 1rem; }
  footer .disclaimer {
    padding-top: 2rem;
    color: #000;
    width: 90%;
    padding-bottom: 3rem; }
