/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
footer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 2rem;
  .footer-wrapper{
    flex-basis:100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    .footer-index{
      width: 432px;
      display: flex;
      justify-content: space-between;
      a{
        color:$black;
        text-decoration: none;
      }
    }
    p{
      padding-top:1rem;
    }
  }
  .disclaimer{
    padding-top: 2rem;
    color: #000;
    width: 90%;
    padding-bottom: 3rem;
  }
}
