/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
body{
  color:$white;
}
p{
  margin:0;
  color:$black;
}
h2{
  color:$black;
}
.margin{
  margin-left:5%;
  margin-right:5%;
}
.tb{
  margin-top:5%;
  margin-bottom:5%;
}
.white-box{
  min-height:30vw;
  background-color: white;
  padding: 3rem 1rem 3rem 2rem;
  box-shadow: 0 0 5px rgba(0,0,0,0.6);
}
.hide{
  display:none;
}
.privacy, .terms{
  p{
    margin-bottom:1rem;
  }
  li{
    margin-bottom:1rem;
  }
}
/*--------------------------------------------------------------
# Order Page
--------------------------------------------------------------*/
.loader{
  display:none;
  width: 200px;
  margin: 0 auto;
  position: fixed;
  bottom: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  img{
    width:100%;
  }
}




/*--------------------------------------------------------------
# CVV BOX
--------------------------------------------------------------*/
.cvv-button{
  border:none;
  background:none;
  color:$black;
  text-decoration: underline;
}
.show{
  display: block !important;
}
.cvv-box{
  display: none;
  position: fixed;
  width:75%;
  margin-left:auto;
  margin-right:auto;
  bottom: 15%;
  left: 12%;
  span{
    font-size: 1.15rem;
    font-weight: bold;
  }
  p,img{
    padding-bottom:1rem;
    color:$black;
  }
  .close-cvv{
    position: absolute;
    right: 1%;
    border: none;
    color: black;
    font-size: 2rem;
    top: 1%;
  }
}

/*--------------------------------------------------------------
# Thank You Page
--------------------------------------------------------------*/
.thank-you-page{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom:5%;
  h2{
    color:$blue;
    font-size: 3.5rem;
  }
  .thank-you-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    p{
      padding-bottom:1rem;
      font-size:1.25rem;
    }
    .number-generator{
      text-align: center;
      p:nth-child(2){
        font-size: 2rem;
        font-weight: bold;
      }
    }
  }
}